<template>
  <div class="home">
    <div class="search">
   
      <form action="/">
      
        <van-search placeholder="请输入搜索关键词" show-action @search="onSearch" background="#f22e00"/>
      </form>
    </div>
    <div class="scroll">
      <van-swipe :autoplay="1500" indicator-color="white">
        <van-swipe-item v-for="swiperPic in swiperPics" :key="swiperPic.index">
          <img :src="swiperPic.src" alt="" class="swiper-mh">
        </van-swipe-item>

      </van-swipe>
    </div>
    <div class="nav">
      <div class="nav-item" v-for="nav in navs" :key="nav.index">
        <router-link :to="{path: '/' + nav.path}">
          <img :src="nav.src" alt="" class="nav-icon">
        </router-link>

      </div>

    </div>
    <div class="recommend">
      <div class="recomm-item" v-for="recom in recoms" :key="recom.index">
        <div class="recom-title" :style="{color:recom.color,fontSize:16+'px'}">淘抢购</div>
        <div class="recom-pic">
          <img src="static/home/cdb.jpg" alt="">
          <img src="static/home/cdb.jpg" alt="">
        </div>
      </div>

    </div>
    <div class="shop-list" :style="{'height':shopWrap+'px'}">
      <van-list finished-text="没有更多了" v-model="goodloding" :finished="finished" @load="onLoad"  :offset="300"
        style="background-color: #f2f2f2;">
        <div class="shop" v-for="good in goods" :key="good.index">
          <img src="static/home/good-one.jpg" alt="" style="max-width:100%;">
          <div class="shop-cont">
            <span class="shop-title">华为荣耀手机</span>
            <span><span class="shop-pay">￥84</span><span class="shop-pay-people">64人已经付款</span></span>
          </div>

        </div>
      </van-list>
    </div>



  </div>
</template>
<script>
  import {
    Swipe,
    SwipeItem,
    Search,
    List
  } from 'vant';
  export default {
    data() {
      return {
        swiperPics: [{
            src: "./static/home/one.jpg"
          },
          {
            src: "./static/home/two.jpg"
          },
          {
            src: "./static/home/three.jpg"
          }
        ],
        navs: [{
            path: "/",
            src: "static/home/nav-icon.png"
          },
          {
            path: "/",
            src: "static/home/nav-icon.png"
          },
          {
            path: "/",
            src: "static/home/nav-icon.png"
          },
          {
            path: "/",
            src: "static/home/nav-icon.png"
          },
          {
            path: "/",
            src: "static/home/nav-icon.png"
          }, {
            path: "/",
            src: "static/home/nav-icon.png"
          },
          {
            path: "/",
            src: "static/home/nav-icon.png"
          }, {
            path: "/",
            src: "static/home/nav-icon.png"
          },
          {
            path: "/",
            src: "static/home/nav-icon.png"
          },
          {
            path: "/",
            src: "static/home/nav-icon.png"
          }
        ],

        recoms: [

          {
            color: "red",
            srcFrist: "static/home/cdb.jpg",
            srcSecond: "static/home/cdb.jpg"
          },
          {
            color: "#35b1ff",
            srcFrist: "static/home/cdb.jpg",
            srcSecond: "static/home/cdb.jpg"
          },
          {
            color: "#F40",
            srcFrist: "static/home/cdb.jpg",
            srcSecond: "static/home/cdb.jpg"
          }, {
            color: "#8d7afb",
            srcFrist: "static/home/cdb.jpg",
            srcSecond: "static/home/cdb.jpg"
          }
        ],
        goodloding: false,
        finished: false,
        shopWrap:2000,
        goods: [{
            name: 1
          },
          {
            name: 1
          },
          {
            name: 1
          },
          {
            name: 1
          }
        ]
      }
    },
    components: {
      [Swipe.name]: Swipe,
      [SwipeItem.name]: SwipeItem,
      [Search.name]: Search,
      [List.name]: List
    },
    methods: {
      onSearch() {},
      onLoad() {
        let reqgood = [{
            name: 2
          },
          {
            name: 2
          },
          {
            name: 2
          },
          {
            name: 2
          }
        ]
        console.log("已经到达底部");
        setTimeout(()=>{
          for(let i=0;i<=3;i++){
            this.goods.push(reqgood[i]);
          }
          this.goodloding=false;
        },2000)
      

      },
    }
  }

</script>
<style>
  .search {
    color: #fff;
  }

  .scroll {
    width: 100%;
    height: 150px;
  }

  .swiper-mh {
    max-width: 100%;
    height: 150px;

  }

  .nav {
    display: flex;
    flex-direction: row;
    flex-flow: wrap;
  }

  .nav-item {
    width: 20%;
    height: 75px;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;

  }

  .nav-icon {
    width: 70px;

  }

  .recommend {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    height: 204px;
    background-color: #f2f2f2;
    padding-top: 6px;
    padding-bottom: 8px;
  }

  .recomm-item {
    width: calc(50% - 1px);
    height: 100px;
    background-color: #fff;
    margin-top: 1px;
    margin-bottom: 1px;
  }

  .recomm-item:nth-of-type(odd) {
    margin-right: 1px;

  }

  .recomm-item:nth-of-type(even) {
    margin-left: 1px;

  }

  .recom-title {
    font-size: 16px;
    text-align: left;
  }

  .recom-pic {
    display: inline-flex;
    flex-direction: row;
    flex-wrap: wrap;
  }

  .recom-pic>img {
    width: 50%;
    height: 79px;
  }

  .shop-list {
    width: 100%;
    background-color: #f2f2f2;

  }

  .shop {
    width: calc(50% - 5px);
    height: 240px;
    float: left;
    margin-top: 5px;
    margin-bottom: 5px;
    background-color: #fff;

  }

  .shop-cont {
    display: flex;
    flex-direction: column;
    text-align: left;
  }

  .shop>img {
    width: 182.5px;
    height: 182.5px;
  }

  .shop-title {
    font-size: 12px;
    margin-left: 4px;
  }

  .shop-pay {
    font-size: 16px;
    float: left;
    color: #F40;
  }

  .shop-pay-people {
    font-size: 14px;
    float: right;
  }

  .shop:nth-of-type(odd) {
    margin-right: 5px;

  }

  .shop:nth-of-type(even) {
    margin-left: 5px;

  }

</style>



